.profilePhoto{display:flex;align-items:center;justify-content:center;width:100%}.profilePhoto .imgWrapper{width:88px;height:88px;overflow:hidden;border-radius:50%;border:4px solid #fff;box-sizing:border-box;vertical-align:middle;text-align:center;box-shadow:0 15px 8px -10px rgba(0,0,0,0.1)}.profilePhoto .imgWrapper img{max-width:100%;top:50%;position:relative;transform:translate(0, -50%)}
