.card{display:flex;flex-direction:column;background-color:#fff;box-shadow:0 40px 40px -40px rgba(0,0,0,0.16);border-radius:16px;margin:0 16px 16px 0}.card:last-child{margin-right:0}.card.heightSmall{height:200px;overflow-y:scroll}.card.heightMedium{height:300px;overflow-y:scroll}.card.heightLarge{height:600px;overflow-y:scroll}.card.heightFull{height:100%;overflow-y:scroll}.card.widthSmall{width:200px;overflow-y:scroll}.card.widthMedium{width:300px;overflow-y:scroll}.card.widthLarge{width:600px;overflow-y:scroll}.card.widthFull{width:100%;overflow-y:scroll}
